<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center flex-grow justify-between mb-base">
      <div class="flex flex-wrap-reverse items-center">
        <vs-button v-if="parent.lgTableData.length > 0" class="p-3 mr-4" @click="exportXls" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
        <vs-button v-if="parent.lgTableData.length > 0" class="p-3 mr-4" @click="exportCsv" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="parent.refreshLgTable" type="filled" icon="fa-sync" icon-pack="far">{{ $t('REFRESH') }}</vs-button>
      </div>
    </div>

    <ag-grid-vue class="ag-theme-balham ag-pivot-layout"
                 :gridOptions="gridOptions"
                 :rowData="parent.lgTableData"
                 :sideBar="sideBar"
                 :statusBar="statusBar">
    </ag-grid-vue>
  </nv-card>
</template>

<script>
import PivotLayoutMixin from '@/mixins/commons/PivotLayoutMixin'
import { AgGridVue } from 'ag-grid-vue'

export default {
  name: 'lg-outstanding-pivot-layout',
  mixins: [PivotLayoutMixin],
  components: {
    AgGridVue
  },
  beforeMount () {
    this.gridOptions.columnDefs = [{
      headerName: this.$t('LG_REFERENCE_NUMBER'),
      field: 'lgNumber'
    }, {
      headerName: this.$t('LG_TYPE'),
      field: 'typeName',
      enableRowGroup: true
    }, {
      headerName: this.$t('BENEFICIARY_NAME'),
      field: 'beneficiaryName',
      enableRowGroup: true
    }, {
      headerName: this.$t('ISSUANCE_DATE'),
      field: 'issuanceDate',
      filter: 'agDateColumnFilter',
      valueFormatter: this.formatDate,
      enableRowGroup: true
    }, {
      headerName: this.$t('CURRENCY'),
      field: 'currencyAbbreviation',
      enableRowGroup: true
    }, {
      headerName: this.$t('LG_AMOUNT'),
      field: 'lgAmount',
      type: 'numericColumn',
      filter: 'agNumberColumnFilter',
      valueFormatter: this.formatNumber,
      enableValue: true
    }]
  }
}
</script>
