<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`LgOutstanding${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import LgOutstandingListLayout from '@/views/trade/lg/lg-outstanding/lg-outstanding-browse/LgOutstandingListLayout'
import LgOutstandingCardLayout from '@/views/trade/lg/lg-outstanding/lg-outstanding-browse/LgOutstandingCardLayout'
import LgOutstandingPivotLayout from '@/views/trade/lg/lg-outstanding/lg-outstanding-browse/LgOutstandingPivotLayout'
import LgOutstandingTableMixin from '@/mixins/trade/lg/LgOutstandingTableMixin'

export default {
  name: 'lg-outstanding-browse',
  mixins: [LgOutstandingTableMixin],
  components: {
    LgOutstandingListLayout,
    LgOutstandingCardLayout,
    LgOutstandingPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadLgTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
