import TradeFinanceService from '@/services/trade/TradeFinanceService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let LgOutstandingTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  data: function () {
    return {
      lgTableData: [],
      lgTableBusy: false,
      lgOutstandingContainer: 'loading-container',
      lgTableTotalRows: 0,
      lgTableCurrentPage: 1,
      lgTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('LG_REFERENCE_NUMBER'),
        field: 'lgNumber',
        format: 'Number'
      }, {
        headerName: this.$t('LG_TYPE'),
        field: 'typeName'
      }, {
        headerName: this.$t('BENEFICIARY_NAME'),
        field: 'beneficiaryName'
      }, {
        headerName: this.$t('ISSUANCE_DATE'),
        field: 'issuanceDate',
        format: 'Date'
      }, {
        headerName: this.$t('CURRENCY'),
        field: 'currencyAbbreviation'
      }, {
        headerName: this.$t('LG_AMOUNT'),
        field: 'lgAmount',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    lgTableNoDataText: function () {
      return this.lgTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    lgTableTotalPages: function () {
      return Math.ceil(this.lgTableTotalRows / this.lgTablePerPage)
    },
    lgTablePagingInfo: function () {
      return this.pagingInfo(this.lgTableCurrentPage, this.lgTablePerPage, this.lgTableTotalRows)
    }
  },
  methods: {
    loadLgTableData (loadingContainerId) {
      let self = this
      this.lgTableBusy = true
      this.lgOutstandingContainer = loadingContainerId || this.lgOutstandingContainer
      this.startLoading(this.lgOutstandingContainer)
      TradeFinanceService.outstandingLgs({
      }).then(function (response) {
        self.stopLoading(self.lgOutstandingContainer)
        self.lgTableBusy = false
        self.lgTablePerPage = response.data.totalRows
        self.lgTableTotalRows = response.data.totalRows
        self.lgTableData = response.data.lgList || []
      }).catch(function (error) {
        self.stopLoading(self.lgOutstandingContainer)
        self.lgTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshLgTable () {
      this.loadLgTableData()
    }
  },
  watch: {
    lgTableCurrentPage: function () {
      this.loadLgTableData()
    }
  }
}

export default LgOutstandingTableMixin
